"use client";
import React from "react";
import { BackgroundGradient } from "./ui/background-gradient";
import { IconAppWindow } from "@tabler/icons-react";

import courseData from "../data/";
import Link from "next/link";

interface Course {
    id: string;
    title: string;
    slug: string;
    description: string;
    price: number;
    instructor: string;
    isFeatured: boolean;
}

function FeaturedCourses() {
    courseData.courses.filter((course: Course) => course.isFeatured).slice(0, 3);
  return (
    <div className="py-12 bg-gray-900">
        <div>
            <div className="text-center">
                <h2 className="text-base text-teal-600 font-semibold tracking-wide">FEATURED COURSES</h2>
                <p className="mt-2 text-3xl leading-8 font-extrabold tracking-tight text-white sm:text-4xl">Learn With The Best</p>
            </div>
        </div>
        <div className="mt-10">
            <div className="gird grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 justify-center">

            </div>
        </div>
        <div className="mt-20 text-center">
            <Link href={"/courses"} 
            className="px-4 py-2 rounded border border-neutral-600 text-neutral-700 bg-white hover:bg-gray-100 transition duration-200"
            >
                View All Course
            </Link> 
        </div>      
    </div>
  );
}

export default FeaturedCourses;
